html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

figure {
	margin: 0;
}

/**
 * Scaffolding
 */
html {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font: normal normal normal 1rem/1.6 -apple-system, BlinkMacSystemFont,
		Helvetica Neue, Helvetica, Arial, sans-serif;
	font-size: 1rem;
}

body {
	color: #404040;
	background: white;
	font-size: 1rem;
}

p,
ol,
ul,
dl,
table {
	margin: 0 0 1.5rem 0;
}

ul li ul {
	margin-bottom: 0;
}

ol li ol {
	margin-bottom: 0;
}

h1,
h2,
h3,
h4,
h5 {
	margin: 1.5rem 0;
	font-weight: 600;
	font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica,
		Arial, sans-serif;
	line-height: 1.2;
	color: #404040;
}

h1:not(:first-child),
h2:not(:first-child),
h3:not(:first-child),
h4:not(:first-child),
h5:not(:first-child) {
	margin: 1.5rem 0;
}

h1:not(:first-child),
h2:not(:first-child),
h3:not(:first-child) {
	margin-top: 2rem;
}

h1 {
	font-size: 1.75rem;
}

h2 {
	font-size: 1.5rem;
}

h3 {
	font-size: 1.25rem;
}

h4 {
	font-size: 1.1rem;
}

h5 {
	font-size: 1rem;
}

@media (min-width: 600px) {
	h1:not(:first-child),
	h2:not(:first-child),
	h3:not(:first-child) {
		margin-top: 2.5rem;
	}
	h1 {
		font-size: 2.25rem;
	}
	h2 {
		font-size: 2rem;
	}
	h3 {
		font-size: 1.75rem;
	}
	h4 {
		font-size: 1.5rem;
	}
	h5 {
		font-size: 1.25rem;
	}
}

a {
	color: #0366ee;
	text-decoration: none;
}

a:hover,
a:active,
a:focus {
	color: #0246a2;
	text-decoration: underline;
}

mark {
	background: #ffeea8;
	padding: 0 0.2rem;
}

blockquote {
	margin: 0 0 1.5rem 0;
	border-left: 16px solid #f0f0f0;
	padding: 0 1.5rem;
	font-size: 1.5rem;
}

blockquote cite {
	display: block;
	margin-top: 1.5rem;
	font-size: 1rem;
	text-align: right;
}

pre {
	border: 0;
	border-radius: 4px;
	background: transparent;
	padding: 1rem;
	tab-size: 2;
	color: #404040;
	font-family: Menlo, monospace;
	font-size: 14px;
	margin: 0 0 1.5rem 0;
}

pre code {
	font-family: Menlo, monospace;
	line-height: 1.2;
}

kbd {
	background-color: #f7f7f7;
	border: 1px solid #ccc;
	border-radius: 3px;
	box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2), 0 0 0 2px #fff inset;
	color: #333;
	display: inline-block;
	font-family: Helvetica, Arial, sans-serif;
	font-size: 13px;
	line-height: 1.4;
	margin: 0 0.1em;
	padding: 0.1em 0.6em;
	text-shadow: 0 1px 0 #fff;
}

:not(pre) > code {
	color: #404040;
	background: transparent;
	font-family: Menlo, monospace;
	font-size: 14px;
	padding: 0 0.2rem;
	border: 1px solid #dedede;
	border-radius: 4px;
}

hr {
	height: 0;
	border: 0;
	border-top: 1px solid #dedede;
}

dt {
	font-weight: 600;
}

dd {
	margin-bottom: 0.5rem;
}

.full-container {
	max-width: 100%;
	padding: 0 1rem;
}

.container,
.small-container,
.medium-container {
	max-width: 1200px;
	padding: 0 1rem;
	margin-left: auto;
	margin-right: auto;
}

.small-container {
	max-width: 800px;
}

.medium-container {
	max-width: 1000px;
}

.content-section {
	padding: 30px 0;
}

@media (min-width: 600px) {
	.content-section {
		padding: 60px 0;
	}
}

/**
 * Grid
 */
.flex-small,
.flex-large {
	padding-left: 1rem;
	padding-right: 1rem;
}

.flex-row {
	margin-left: -1rem;
	margin-right: -1rem;
}

.flex-row {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.flex-small,
.flex-large {
	flex-basis: 100%;
	margin-bottom: 1rem;
}

/* Small screen breakpoint */
@media (min-width: 600px) {
	.flex-small {
		flex: 1;
		margin-bottom: 0;
	}
	.flex-small.half {
		flex: 0 0 calc(1 / 2 * 100%);
	}
	.flex-small.one-fourth {
		flex: 0 0 calc(1 / 4 * 100%);
	}
	.flex-small.three-fourths {
		flex: 0 0 calc(3 / 4 * 100%);
	}
	.flex-small.one-third {
		flex: 0 0 calc(1 / 3 * 100%);
	}
	.flex-small.two-thirds {
		flex: 0 0 calc(2 / 3 * 100%);
	}
}

/* Large screen breakpoint */
@media (min-width: 1000px) {
	.flex-large {
		flex: 1;
		margin-bottom: 0;
	}
	.flex-large.half {
		flex: 0 0 calc(1 / 2 * 100%);
	}
	.flex-large.one-fourth {
		flex: 0 0 calc(1 / 4 * 100%);
	}
	.flex-large.three-fourths {
		flex: 0 0 calc(3 / 4 * 100%);
	}
	.flex-large.one-third {
		flex: 0 0 calc(1 / 3 * 100%);
	}
	.flex-large.two-thirds {
		flex: 0 0 calc(2 / 3 * 100%);
	}
}

/**
 * Helpers
 */
.clearfix::before,
.clearfix::after {
	content: ' ';
	display: block;
}

.clearfix:after {
	clear: both;
}

.text-left {
	text-align: left;
}

.text-right {
	text-align: right;
}

.text-center {
	text-align: center;
}

.text-justify {
	text-align: justify;
}

.block {
	display: block;
}

.inline-block {
	display: inline-block;
}

.inline {
	display: inline;
}

.vertical-center {
	display: flex;
	align-items: center;
	justify-content: center;
}

.responsive-image {
	max-width: 100%;
	height: auto;
}

.show {
	display: block !important;
}

.hide {
	display: none !important;
}

.invisible {
	visibility: hidden;
}

.no-padding-top {
	padding-top: 0;
}

.no-padding-bottom {
	padding-bottom: 0;
}

.padding-top {
	padding-top: 2rem;
}

.padding-bottom {
	padding-bottom: 2rem;
}

.no-margin-top {
	margin-top: 0;
}

.no-margin-bottom {
	margin-bottom: 0;
}

.margin-top {
	margin-top: 2rem;
}

.margin-bottom {
	margin-bottom: 2rem;
}

.alternate-background {
	background: #fafafa;
	color: #404040;
}

.space-between {
	justify-content: space-between;
}

.justify-center {
	justify-content: center;
}

.align-center {
	align-items: center;
}

.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

/**
 * Buttons
 */
.button,
a.button,
button,
[type='submit'],
[type='reset'],
[type='button'] {
	-webkit-appearance: none;
	display: inline-block;
	border: 1px solid #0366ee;
	border-radius: 4px;
	background: #0366ee;
	color: #ffffff;
	font-weight: 600;
	font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica,
		Arial, sans-serif;
	font-size: 1rem;
	text-transform: none;
	padding: 0.75rem 1.25rem;
	margin: 0 0 0.5rem 0;
	vertical-align: middle;
	text-align: center;
	cursor: pointer;
	text-decoration: none;
	line-height: 1;
}

.button:hover,
a.button:hover,
button:hover,
[type='submit']:hover,
[type='reset']:hover,
[type='button']:hover {
	border: 1px solid #0250bc;
	background: #0250bc;
	color: #ffffff;
	text-decoration: none;
}

.button:focus,
.button:active,
a.button:focus,
a.button:active,
button:focus,
button:active,
[type='submit']:focus,
[type='submit']:active,
[type='reset']:focus,
[type='reset']:active,
[type='button']:focus,
[type='button']:active {
	border: 1px solid #0250bc;
	background: #0250bc;
	color: #ffffff;
	text-decoration: none;
}

.button::-moz-focus-inner,
a.button::-moz-focus-inner,
button::-moz-focus-inner,
[type='submit']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='button']::-moz-focus-inner {
	border: 0;
	padding: 0;
}

.accent-button,
