a.accent-button:active {
	color: #ffffff;
	border: 1px solid #1cb864;
	background: #1cb864;
}

.muted-button,
a.muted-button {
	background: transparent;
	border: 1px solid #cdcdcd;
	color: #4e4e4e;
}

.muted-button:hover,
.muted-button:focus,
.muted-button:active,
